@import "../helpers/_mixin.scss";
/*
	==========================================================================
	* Datagrid 2.0
	==========================================================================
*/
.table {
  margin-bottom: 0;
  display: unset !important;
  .widget-datagrid-col-select {
    padding: 3px 8px !important;
    min-width: unset !important;
    input[type="checkbox"] {
      padding: 7px;
      &::before {
        height: 16px;
        width: 16px;
      }
      &::after {
        margin: 4px 3px 4px 4px;
      }
      &:indeterminate::after {
        width: 10px;
        margin: 5px 3px;
      }
    }
  }
  .table-content {
    width: 100%;
    .tr {
      .td {
        padding: 8px 16px;
        border-top-width: 0px;
        border-color: $datagrid2-border-color;
        font-size: $datagrid2-font-size;
        color: $datagrid2-font-color;
        background-color: $datagrid2-background-color;
        &:has(.mx-dropdown) {
          padding: 2px 16px;
        }
        &:has(.td-custom-content) * {
          overflow: inherit;
        }
        .td-custom-content {
          display: flex;
          align-items: center;
          .form-group {
            margin-bottom: 0;
            width: 100%;
            padding-right: 12px;
          }
          .mx-image,
          .mx-link {
            display: inline-block;
            &.popup-icon {
              width: 24px;
              height: 24px;
              border-radius: 20px;
              margin-top: 4px;
              padding: 2px;
              &:hover {
                background-color: $datagrid2-icon-hover-background-color;
                box-shadow: 0px 0px 0px 1px $datagrid2-icon-hover-border-color;
              }
              &:active,
              &:focus {
                color: $datagrid2-popupmenu-active-font-color;
                background-color: $datagrid2-icon-active-background-color;
              }
            }
            &.icon-hover:hover {
              background-color: $gsk-primary-light;
            }
          }
          .popupmenu .popupmenu-trigger {
            align-items: center;
            display: flex;
          }
          .mx-dropdown::after {
            right: 16px;
          }
        }
      }
      &.selected,
      &.tr-selected,
      &.selected:focus,
      &.selected:hover {
        .td {
          background-color: $datagrid2-selected-background-color !important;
          color: $datagrid2-selected-font-color !important;
        }
        &:has(.widget-datagrid-col-select) {
          .td {
            color: $datagrid2-font-color !important;
            background-color: $datagrid2-background-color !important;
          }
        }
      }
      &.new-row {
        .td {
          background-color: $datagrid2-newrow-background-color !important;
        }
      }
    }
  }

  .pagination-bar {
    margin-right: 0;
    margin-left: 0;
    button:disabled {
      opacity: 0.3;
    }
    .paging-status {
      font-size: $datagrid2-footer-font-size;
      padding: 0px;
      align-self: center;
      min-width: 60px;
      text-align: center;
      margin-left: 12px;
      color: $datagridshared-pagingbar-font-color;
    }

    .pagination-button {
      @include material-icons;
      min-width: 60px;
      box-shadow: none;
      color: $datagridshared-pagingbar-font-color;
      font-size: $datagridshared-pagingbar-paging-button-font-size !important;
      display: inline-block;
      &:hover {
        color: $datagridshared-pagingbar-font-color;
      }
      &:last-child {
        min-width: unset;
        margin-left: 25px;
      }
      .glyphicon,
      svg {
        display: none;
      }
    }
    button[aria-label="Go to first page"] {
      &::before {
        content: $datagridshared-pagingbar-paging-first-icon;
      }
    }
    button[aria-label="Go to previous page"] {
      &::before {
        content: $datagridshared-pagingbar-paging-previous-icon;
      }
    }
    button[aria-label="Go to next page"] {
      &::before {
        content: $datagridshared-pagingbar-paging-next-icon;
      }
    }
    button[aria-label="Go to last page"] {
      &::before {
        content: $datagridshared-pagingbar-paging-last-icon;
      }
    }
  }

  .tr:first-child .th {
    border: none;
    border-bottom-width: 1px;
    border-bottom-style: solid;
    border-color: $datagrid2-first-row-border-color;
    align-items: flex-end;
  }

  .column-selector {
    padding: 8px 8px 8px 0px !important;
    min-width: unset !important;
    .column-selector-content {
      align-self: end;
      .column-selector-button {
        min-width: unset;
        padding: 10px;
        min-height: 40px;
        min-width: 40px;
        background-image: $datagridshared-columnselector-icon !important;
        background-repeat: no-repeat;
        background-position: center;
        border-color: $datagridshared-columnselector-border-color !important;
        padding: 6px;
        background-size: 24px 26px;
        svg {
          // width: 18px;
          // margin: 0;
          display: none;
        }
        &:hover,
        &:focus {
          background-color: $datagridshared-columnselector-hover-bg-color !important;
          border-color: $datagridshared-columnselector-hover-border-color !important;
          box-shadow: none !important;
        }
        &[aria-expanded="true"] {
          background-image: $datagridshared-columnselector-selected-icon !important;
          background-color: $datagridshared-columnselector-expanded-bg-color !important;
          box-shadow: none !important;
          border-color: $datagridshared-columnselector-expanded-border-color !important;
        }
      }
    }
  }

  .th {
    padding: 8px 16px;
    border-color: $datagrid2-table-head-border-color;
    color: $datagrid2-table-head-font-color;
    z-index: 1;
    .column-container {
      align-self: flex-start;
      &:has(.filter-container),
      &:has(.dropdown-container) {
        align-self: flex-end;
      }
    }

    .column-header {
      span {
        font-size: $datagrid2-table-head-font-size;
        font-weight: $datagrid2-table-head-font-weight;
      }
      svg {
        path {
          display: none;
        }
        height: 20px;
        width: 20px;
        flex: 0 0 15px;
        align-self: flex-end;
        margin-top: 2px;
        &[data-icon="arrows-alt-v"] {
          font-size: $datagrid2-table-head-font-size;
          font-weight: $datagrid2-table-head-font-weight;
        }
        // &::after {
        //   background: $datagridshared-searchbar-sort-ascending-icon no-repeat;
        // }
        // &[data-icon="long-arrow-alt-down"] {
        //   background: $datagridshared-searchbar-sort-descending-icon no-repeat;
        // }
      }
    }

    .column-resizer {
      &:hover,
      &:active {
        .column-resizer-bar {
          background-color: $datagrid2-table-resizer-background-color;
        }
      }
    }

    .filter {
      margin-top: 8px;

      .filter-container {
        .filter-selector {
          .filter-selector-content {
            .filter-selector-button {
              @extend .btn-primary;
              min-width: 40px;
              height: 40px;
              border-top-left-radius: 8px;
              border-bottom-left-radius: 8px;
              font-weight: $datagrid2-table-filter-font-weight;
              border-color: $datagrid2-filter-border-color !important;
              box-shadow: none !important;

              &::before {
                font-size: $font-size-default;
              }
              &:hover {
                background-color: $datagrid2-table-filter-selector-hover-bg-color !important;
              }
              &[aria-expanded="true"] {
                background-color: $datagrid2-table-filter-selector-expanded-bg-color !important;
                color: $datagrid2-table-filter-selector-expanded-font-color !important;
                border-color: $datagrid2-table-filter-selector-expanded-border-color !important;
              }
            }
          }
        }
        &:has(.filter-input:active),
        &:has(.filter-input:focus) {
          .filter-selector-button {
            border-color: $datagridshared-columnselector-active-border-color !important;
          }
        }
        .btn-calendar {
          @include material-icons;
          border-color: transparent !important;
          box-shadow: none !important;
          position: absolute;
          right: 24px;
          font-size: $datagridshared-searchbar-calendar-icon-font-size !important;
          padding: 6px 4px;
          &::before {
            content: $datagridshared-searchbar-calendar-icon;
          }
          .glyphicon-calendar,
          svg {
            display: none;
          }
        }
        &:has(.react-datepicker__close-icon) {
          .btn-calendar {
            display: none;
          }
        }

        input {
          border-left: none;
          height: 40px;
        }
        .react-datepicker-wrapper {
          .filter-input {
            padding: 8px 26px 8px 8px;
          }
          .react-datepicker__close-icon {
            background: $datagridshared-searchbar-calendar-close-icon no-repeat !important;
            margin-top: 13px;
            width: 25px;
            height: 25px;
            &::after {
              display: none;
            }
          }
        }
      }
      .dropdown-container {
        .dropdown-triggerer {
          background-image: none;
        }
        position: relative;
        &::after {
          @include material-icons;
          font-size: $input-icon-font-size !important;
          content: $input-dropdown-icon !important;
          position: absolute;
          right: 8px;
          top: 8px;
        }
      }
    }
  }
}
.horizontal-scroll .table {
  .table-content {
    overflow-x: auto;
    .tr:first-child .th {
      min-width: 10vw;
      &:has(.react-datepicker-wrapper) {
        min-width: 12vw;
      }
    }
  }
}

//scroll for mobile screens
@media screen and (max-width: $m) {
  .table {
    .table-content {
      overflow-x: auto;
      .tr:first-child .th {
        min-width: 120px;
        &:has(.react-datepicker-wrapper) {
          min-width: 150px;
        }
      }
    }
  }
}

.column-selectors {
  margin: 0;
  border: 2px solid $datagridshared-columnselector-dropdown-border-color;
  border-radius: 16px;
  box-shadow: 2px 2px 18px
    $datagridshared-columnselector-dropdown-boxshadow-color;
  padding: 4px;
  li {
    font-size: $datagridshared-columnselector-font-size;
    padding: 4px;
    border-radius: 8px;
    &:hover {
      background-color: $datagridshared-columnselector-dropdown-hover-bg-color;
    }
    label {
      margin: 8px 8px 8px 4px;
    }
  }
}

//Filter dropdown for Data Grid 2
.filter-selectors {
  border: 2px solid $datagrid2-filter-border-color;
  border-radius: 8px;
  box-shadow: 0px 2px 16px $datagrid2-filter-boxshadow-color;

  > li {
    padding: 8px;
    border-radius: 2px;
    width: 100%;
    text-transform: none;
    color: $datagrid2-filter-font-color;
    font-size: $datagrid2-filter-font-size;
    font-weight: $datagrid2-filter-font-weight;
    letter-spacing: normal;

    @include hfv-states {
      background-color: $datagrid2-filter-status-background-color;
    }

    &.filter-selected,
    &.filter-selected:hover,
    &.filter-selected:active,
    &.filter-selected:focus {
      color: $datagrid2-filter-selected-font-color;
      background-color: $datagrid2-filter-selected-background-color;
    }
  }
}
//Filter dropdown with checkboxes for Data Grid 2
.dropdown-list {
  border: 2px solid $datagrid2-dropdown-border-color;
  border-radius: 8px;
  box-shadow: 0px 2px 16px $datagrid2-dropdown-boxshadow-color;

  > li {
    padding: 4px 8px;
    border-radius: 2px;
    width: 100%;
    text-transform: none;
    color: $datagrid2-dropdown-font-color;
    font-size: $datagrid2-dropdown-font-size;
    font-weight: $datagrid2-dropdown-font-weight;
    letter-spacing: normal;
    &.filter-selected,
    &.filter-selected:hover,
    &.filter-selected:active,
    &.filter-selected:focus {
      color: $datagrid2-filter-selected-font-color;
      background-color: $datagrid2-filter-selected-background-color;
    }
    @include hfv-states {
      background-color: $datagrid2-hover-background-color;
    }
    input[type="checkbox"] {
      // width: 18px;
      // height: 18px;
      &::before {
        border-width: 2px;
        border-color: $datagrid2-dropdown-checkbox-border-color;
      }
      &:checked {
        &::before {
          border-color: $datagrid2-dropdown-checkbox-checked-border-color;
          background-color: $datagrid2-dropdown-checkbox-checked-background-color;
        }
      }
      &.disabled,
      &:disabled,
      &[disabled] {
        &::before {
          border: none;
          background-color: $datagrid2-dropdown-checkbox-disabled-background-color;
        }
      }
      &:focus {
        outline-color: $datagrid2-dropdown-checkbox-focus-outline-color;
      }
    }
    label {
      margin: 8px 4px 8px 4px;
    }
    &.form-group {
      .control-label {
        font-size: $datagrid2-dropdown-label-font-size;
      }
    }
  }
}
//Date picker for Data Grid 2
.react-datepicker {
  border: 1px solid $reactdatepicker-border-color;
  border-radius: 4px;
  margin-top: 4px;
  background: $reactdatepicker-background-color;
  box-shadow: 0px 4px 10px 1px $reactdatepicker-boxshadow-color;
  font-size: $reactdatepicker-font-size;
  color: $reactdatepicker-font-color;
  .react-datepicker__month-container .react-datepicker__header__dropdown {
    margin: 4px 0 4px 0;
  }
  .react-datepicker__navigation--previous {
    left: 4px;
    span {
      &::before {
        content: $calendar-month-previous-icon;
      }
    }
  }
  .react-datepicker__navigation--next {
    right: 4px;
    span {
      &::before {
        content: $calendar-month-next-icon;
      }
    }
  }
  .react-datepicker__navigation {
    .react-datepicker__navigation-icon {
      @include material-icons;
      font-size: $calendar-month-icon-font-size !important;
      padding: 0;
      &::before {
        border-color: $reactdatepicker-navigationicon-border-color;
        border-width: 2px 2px 0 0;
      }
    }
  }
  .react-datepicker-time__header,
  .react-datepicker-year-header,
  .react-datepicker__current-month {
    color: $reactdatepicker-header-font-color;
    display: none;
  }
  select {
    border: 1px solid $reactdatepicker-select-border-color;
    border-radius: 4px;
    color: $reactdatepicker-select-font-color;
    padding: 2px;
    &:hover,
    &:focus {
      border-color: $reactdatepicker-header-select-border-color !important;
      cursor: pointer;
    }
  }
  .react-datepicker__day-names {
    .react-datepicker__day-name {
      color: $reactdatepicker-day-font-color;
      font-weight: $reactdatepicker-day-font-weight;
      letter-spacing: 1px;
    }
  }
  .react-datepicker__week {
    .react-datepicker__day {
      height: 30px;
      width: 30px;
      color: $reactdatepicker-week-font-color;
      padding: 1px 2px 1px 1px;
      &:hover {
        background-color: $reactdatepicker-week-hover-background-color;
        color: $reactdatepicker-week-hover-font-color;
        box-shadow: 0 0 0 2px $reactdatepicker-day-hover-border-color;
      }
    }
    .react-datepicker__day--outside-month {
      color: $reactdatepicker-day-outsidemonth-font-color;
    }
    .react-datepicker__day--today {
      border-radius: 20px;
      box-sizing: border-box;
      background-color: $calendar-today-background-color;
      color: $calendar-today-font-color;
    }
    .react-datepicker__day--selected,
    .react-datepicker__day--selected:hover {
      background-color: $reactdatepicker-selected-day-background-color;
      color: $reactdatepicker-selected-day-font-color;
      box-shadow: none !important;
    }
  }
}

.table-striped .table .table-content .tr {
  &:nth-child(odd) .td {
    background-color: $datagrid2-striped-even-background-color;
    border-bottom-width: 0px;
  }

  &:nth-child(even) .td {
    background-color: $datagrid2-striped-odd-background-color;
    border-bottom-width: 0px;
  }
}
.table-bordered-vertical {
  .table .table-content .tr .th {
    border-left-width: 1px;
    border-left-style: solid;
    border-color: $datagrid2-border-color;
    &:last-child {
      border-right-width: 1px;
      border-right-style: solid;
    }
    &.column-selector {
      border-left-width: 0px;
    }
  }
}
.table-bordered-all {
  .table .table-content .tr .th {
    border-left-width: 1px;
    border-left-style: solid;
    border-top-width: 1px;
    border-top-style: solid;
    border-color: $datagrid2-border-color;
    &:last-child {
      border-right-width: 1px;
      border-right-style: solid;
    }
    &.column-selector {
      border-left-width: 0px;
    }
  }
}
.table-compact {
  .table {
    .column-selector .column-selector-content .column-selector-button {
      min-height: 32px;
      min-width: 32px;
      background-size: 18px 20px;
    }
    .table-content {
      input[type="checkbox"],
      input[type="checkbox"].three-state-checkbox,
      input[type="checkbox"].three-state-checkbox:not(:indeterminate),
      input[type="checkbox"].three-state-checkbox {
        height: 18px;
        width: 18px;
        padding: 2px !important;
        &:hover {
          box-shadow: 0px 0px 0px 3px $checkbox-hover-border-color;
        }
        &::before {
          width: 14px !important;
          height: 14px !important;
        }
        &::after {
          margin: 3px !important;
          width: 8px !important;
          height: 5px;
        }
      }
      .tr {
        .td {
          padding: 4px 16px;
          font-size: $datagrid2-tablecompact-font-size;
        }
        &:first-child .th {
          padding: 4px 16px;
          .column-header span {
            font-size: $datagrid2-tablecompact-head-font-size;
          }
          .filter {
            .filter-container {
              .btn-calendar {
                margin-top: 3px;
              }
              .filter-selector
                .filter-selector-content
                .filter-selector-button {
                min-width: 32px;
                min-height: 32px;
                height: 32px;
              }
              input {
                height: 32px;
              }
            }
            .dropdown-container .dropdown-triggerer {
              height: 32px;
            }
          }
        }
      }
    }
  }
}
.table-hover .table .table-content {
  .tr {
    &:hover .td {
      background-color: $datagrid2-hover-background-color;
    }

    &.selected:hover .td {
      background-color: $datagrid2-selected-hover-background-color;
    }
  }
}

// TEMP Datagrid 2 styles to show scrolling functionality
// .table-bordered-horizontal {
//   .table-content {
//     height: 200px;
//   }
// }

@include dense-layout {
  .table {
    .th .filter .filter-container {
      .filter-selector .filter-selector-content .filter-selector-button,
      input {
        height: 32px;
      }

      .btn-calendar {
        right: 36px;
        padding: 0;
        margin: 2px 0px;
        min-width: 24px;
        height: 24px;
      }
    }

    .column-selector .column-selector-content .column-selector-button {
      min-width: 32px;
      min-height: 32px;
      background-size: 20px;
    }

    .table-content .tr .td {
      padding: 4px 16px;
      font-size: $datagrid2-tablecompact-font-size;
    }
    .th {
      .column-header span,
      .filter .form-control,
      .filter .filter-selector-button {
        font-size: $datagrid2-tablecompact-font-size;
      }
      .filter {
        .filter-selector-button::before {
          font-size: 14px !important;
          height: 14px;
        }
        .dropdown-container {
          &::after {
            top: 4px;
          }
        }
      }
    }
  }
  .table {
    .th .filter .filter-container {
      .react-datepicker-wrapper .react-datepicker__close-icon {
        margin-top: 9px;
      }
      .react-datepicker-wrapper .filter-input {
        padding: 4px 26px 4px 8px !important;
      }
    }
    .widget-datagrid-col-select:has(input[type="checkbox"]) {
      input[type="checkbox"] {
        height: 22px;
        width: 22px;
        padding: 3px !important;
        &:hover {
          box-shadow: 0px 0px 0px 3px $checkbox-hover-border-color;
        }
        &::after {
          margin: 4px !important;
          width: 8px !important;
          height: 5px;
        }
      }
    }
  }
}
