.has-focus input,
.has-focus textarea,
.has-focus select {
  border-color: $input-hasfocus-border-color !important;
}
.mx-compound-control
{
  max-width: $input-max-width;
}
.form-control {
  max-width: $input-max-width;
  box-sizing: border-box;
  padding: 8px;
  border: 1px solid $input-border-color;
  border-radius: calc(#{$border-radius-default} * 2);
  background-color: $input-background-color;
  color: $input-font-color;
  font-size: $input-font-size;
  line-height: 24px;

  &::placeholder,
  &::-webkit-input-placeholder {
    color: $input-placeholder-font-color;
    font-style: $input-placeholder-font-style;
  }
  &:active,
  &:focus,
  &:not([readonly]):focus {
    border-color: $input-status-border-color;
    // box-shadow: 0px 0px 0px 0.3px $brand-black;
  }
  &.disabled,
  &:disabled,
  &[disabled] {
    // border-color: $gray-primary;
    background-color: $input-disabled-background-color;
  }
}

.mx-textarea
{
  .mx-textarea-counter
  {
    font-weight: $input-textarea-counter-font-weight;
    color :$input-textarea-counter-font-color;
  }
}



@include dense-layout {
  .form-control {
    padding: 4px 8px !important;
  }
  .form-group
  {
    &.has-error,&.has-success ,&.has-warning ,&.has-info 
    {
      &::after 
      {
        bottom: 6px;
      }
    }
  }
}
